import Head from "next/head";
import Header from "../components/header";

const HeadContents = () => (
  <div>
    <title>Rent-A-Roof</title>
    <link
      rel="stylesheet"
      href="https://stackpath.bootstrapcdn.com/bootstrap/4.2.1/css/bootstrap.min.css"
    />
    <link
      rel="stylesheet"
      href="https://use.fontawesome.com/releases/v5.6.3/css/all.css"
      integrity="sha384-UHRtZLI+pbxtHCWp1t77Bi1L4ZtiqrqD80Kn4Z8NTSRyMA2Fd33n5dQ8lWUE00s/"
      crossorigin="anonymous"
    />
  </div>
);

export default () => (
  <div>
    <Head>
      <HeadContents />
    </Head>
    <Header />
    <div id="landing-bg">
      <div id="landing-text">
        <h2>A home for everyone</h2>
        <input placeholder="Type Suburb" />
      </div>
    </div>

    {/* we can map all of these locations actually from the data store */}
    <div id="locations">
      <div className="row">
        <div className="column">
          <h3>
            <a href="">Gauteng</a>
          </h3>
        </div>
        <div className="column">
          <h3>
            <a href="">Western Cape</a>
          </h3>
        </div>
        <div className="column">
          <h3>
            <a href="">KwaZulu Natal</a>
          </h3>
        </div>
      </div>
      <div className="row">
        <div className="column">Free State</div>
        <div className="column">Eastern Cape</div>
        <div className="column">Northern Cape</div>
      </div>
      <div className="row">
        <div className="column">North West</div>
        <div className="column">Mpumalanga</div>
        <div className="column">Limpopo</div>
      </div>
    </div>
  </div>
);
